import { Fragment, useEffect, useState } from "react";
import classes from "./Products.module.css";
import ProductsNavBar from "./ProductsNavbar";
import { useLoaderData, useParams, useSearchParams } from "react-router-dom";
import ProductsItem from "./ProductItem";
function Products() {
  // state lu danh sch sn phm  lc
  const [productsList, setProductsList] = useState([]);
  // ly params t URL
  const [searchParams] = useSearchParams();
  // ly category ngoi dng mun vo danh mc sn phm no
  const category = searchParams.get("category");
  // danh sch sn phm  ly t api
  const productsListLoader = useLoaderData();
  // s dng useEffect  lc danh sch sn phm khi params b thay i
  useEffect(() => {
    // nu danh mc l all, th setProductsList l tt c sn phm

    if (category === "all") {
      setProductsList(productsListLoader);
    } else {
      // cn khng th loc danh sch sn phm v set va ProductsList
      setProductsList(
        productsListLoader.filter((product) => product.category === category)
      );
    }
  }, [category]);

  return (
    <Fragment>
      <div className=" bg-body-tertiary d-flex p-5 justify-content-between align-items-center">
        <div className=" py-5 px-4 ">
          <h5 className="fs-1">SHOP </h5>
        </div>
        <div className=" py-5 px-4">
          <p className="text-secondary">SHOP</p>
        </div>
      </div>

      <div className={classes.products}>
        <div>
          <h4 className="fw-medium fs-4">CATEGORIES</h4>
          <ProductsNavBar />
        </div>

        <div>
          <div className="d-flex justify-content-between">
            <input
              className={classes.search}
              placeholder="Enter Search Here!"
            ></input>
            <select className={`form-select ${classes.select}`}>
              <option>Default Sorting</option>
              <option>Price</option>
              <option>Product</option>
            </select>
          </div>
          <div className={`mt-4 ${classes["products_list"]}`}>
            {productsList.map((product) => (
              <ProductsItem key={product._id} product={product} />
            ))}
            {productsList.length === 0 && <p>No Product</p>}
          </div>
        </div>
      </div>
    </Fragment>
  );
}
export default Products;
