import { Fragment } from "react";
import { Link } from "react-router-dom";

function HistoryItem(props) {
  // s dng useDispatch  c th chy hm action trong redux
  // ly tng tin v i li theo format tin vnd
  // totalPrice l s nn s dng toString  chuyn v kiu d liu string
  const totalPrice = props.order.totalPrice
    .toString()
    .replace(/\B(?=(\d{3})+(?!\d))/g, ".");

  return (
    <Fragment>
      <tr>
        <td className="text-center py-3">{props.order._id}</td>
        <td className="text-center py-3 ">{props.order.userId}</td>
        <td className="text-center py-3 ">{props.order.fullName}</td>
        <td className="text-center py-3 ">{props.order.phone}</td>
        <td className="text-center py-3 ">{props.order.address}</td>
        <td className="text-center py-3 ">{totalPrice} VND</td>
        <td className="text-center py-3 ">{props.order.delivery}</td>
        <td className="text-center py-3 ">{props.order.status}</td>
        <td className="text-center py-3 ">
          <Link
            to={`/history/${props.order._id}`}
            className="text-decoration-none text-black border border-2 border-dark px-2 py-2"
          >
            View
          </Link>
        </td>
      </tr>
    </Fragment>
  );
}

export default HistoryItem;
