import { json } from "react-router-dom";
import History from "../components/Cart/History";

function HistoryPage() {
  return <History />;
}

export default HistoryPage;

export async function loader({ params, request }) {
  console.log("there");
  const userId = localStorage.getItem("userId");
  console.log(userId);
  // s dng hm bt ng b  fetch data t firebase
  const response = await fetch(
    "https://boutiquebe-60a72a49dd20.herokuapp.com//order",
    {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({ userId: userId }),
    }
  );

  // nu li th tr v messesge khng th ly d liu sn phm
  if (!response.ok) {
    throw json({ message: "Could not fetch order data" }, { status: 500 });
  }
  //cn khng th chuyn i qua json
  // v tr v kt qu
  const resData = await response.json();
  console.log(resData);
  return resData;
}
