import { createSlice } from "@reduxjs/toolkit";

const popupSlice = createSlice({
  name: "popup",
  // SHOW_POPUP  lu d liu khi ngi dng click vo sn phm
  // HIDE_POPUP  hin th trng thi popup ang c hin th hay khng
  initialState: {
    SHOW_POPUP: [],
    HIDE_POPUP: false,
  },
  reducers: {
    // ly d liu c tr v v set vo SHOW_POPUP
    // v hin th popup
    showPopup(state, action) {
      const productDetail = action.payload;
      state.SHOW_POPUP = productDetail;
      state.HIDE_POPUP = true;
    },
    // hide pop up  tt popup
    hidePopup(state, action) {
      state.HIDE_POPUP = false;
    },
  },
});
// export action  c th thao tc vi redux
export const popupActions = popupSlice.actions;

export default popupSlice;
