import { createSlice } from "@reduxjs/toolkit";

export const authSlice = createSlice({
  name: "auth",
  initialState: {
    // state trng thi login
    isAuth: false,
    // state lu d liu user ang ng nhp
    userId: null,
    token: null,
    email: null,
  },
  reducers: {
    ON_LOGIN(state, action) {
      // ly d li c tr v khi ng nhp thnh cng
      const userLogin = action.payload;
      // nu userLogin c tr v th set isLogin thnh true
      // v set UserLogin
      if (userLogin) {
        state.isAuth = true;
        state.userId = userLogin.userId;
        state.token = userLogin.token;
        state.email = userLogin.email;
        localStorage.setItem("token", userLogin.token);
        localStorage.setItem("userId", userLogin.userId);
        localStorage.setItem("email", userLogin.email);
      } else {
        // cn khng th isLogin tr v false
        // v userLogin tr v null
        state.isLogin = false;
        state.userId = null;
        state.token = null;
      }
      console.log(state.userLogin);
    },
    // khi click vo logout
    // set isLogin l false
    // userLogin l null
    ON_LOGOUT(state, action) {
      state.isAuth = false;
      state.userId = null;
      state.token = null;
      localStorage.removeItem("token");
      localStorage.removeItem("userId");
      localStorage.removeItem("email");
    },
  },
});

export const authActions = authSlice.actions;
