import { createSlice } from "@reduxjs/toolkit";
import { getCart } from "../components/Until/cart";

export const cartSlide = createSlice({
  name: "cart",
  // items  lu danh sch sn phm trong gi hng
  // totalQuantify tng s lng sn phm trong danh sch
  // totalPrice  lu tng tin cc sn phm trong gi hng
  initialState: {
    items: [],
    totalQuantity: 0,
    totalPrice: 0,
  },
  reducers: {
    // func thm sn phm vo gi hng v c s lng sn phm
    ADD_CART(state, action) {
      const newItem = action.payload;
      // tm xem c sn phm  trong gi hng hay cha
      const existingItem = state.items?.find((item) => item.id === newItem.id);
      // nu khng c th thm sn phm  vo gi hng
      if (!existingItem) {
        state.items.push({
          id: newItem.id,
          price: newItem.price,
          img: newItem.img,
          quantity: newItem.quantity,
          totalPrice: newItem.price * newItem.quantity,
          name: newItem.name,
        });
        // sau  set li tng tin v s lng sn phm
        state.totalPrice = state.totalPrice + newItem.price * newItem.quantity;
        state.totalQuantity = state.totalQuantity + newItem.quantity;
      } else {
        // cn khng th thay i s lng ca sn phm  c trong gi hang
        existingItem.quantity = existingItem.quantity + newItem.quantity;
        existingItem.totalPrice =
          existingItem.totalPrice + newItem.price * newItem.quantity;
        state.totalPrice = state.totalPrice + newItem.price * newItem.quantity;
        state.totalQuantity = state.totalQuantity + newItem.quantity;
      }
    },
    UPDATE_CART(state, action) {
      // ly id ca sn phm

      const id = action.payload;
      // v tm sn phm c trong gi hng hay khng
      const existingItem = state.items.find((item) => item.id === id);
      // nu sn phm cn 1 th xa sn phm khi gi hng

      if (existingItem.quantity === 1) {
        state.items = state.items.filter((item) => item.id !== id);
        state.totalQuantity--;
        state.totalPrice = state.totalPrice - existingItem.price;
      } else {
        // cn khng th tr bt i s lng sn phm trong gi hangg
        existingItem.quantity--;
        existingItem.totalPrice = existingItem.totalPrice - existingItem.price;
        state.totalQuantity--;
        state.totalPrice = state.totalPrice - existingItem.price;
      }
    },
    DELETE_CART(state, action) {
      // tm id sn phm cn xa
      const id = action.payload;
      // tm xem l sn phm no
      const existingItem = state.items.find((item) => item.id === id);
      // totalQuantity tr i s lng sn phm hin xa i
      state.totalQuantity = state.totalQuantity - existingItem.quantity;
      // set li tng tin ca sn ophm
      state.totalPrice =
        state.totalPrice - existingItem.price * existingItem.quantity;
      // filter li items
      state.items = state.items.filter((item) => item.id !== id);
    },
    // func dng  y d liu t localstorage vo
    LOAD_CART(state, action) {
      const data = action.payload;
      console.log(data);
      if (data?.items) {
        console.log(data);
        state.items = data.items;
        state.totalPrice = data.totalPrice;
        state.totalQuantity = data.totalQuantity;
      }
    },
  },
});

export const cartActions = cartSlide.actions;
